#avion-1 {
    -webkit-animation: paper-plane-scoping 4s alternate infinite;
            animation: paper-plane-scoping 4s alternate infinite; 
    -webkit-animation-timing-function: linear; 
            animation-timing-function: linear;
    z-index: 0;
  }
#avion-2 {
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation: rotate 4s forwards infinite;
          animation: rotate 4s forwards infinite;
  -webkit-transform-origin: center center;
          transform-origin: center center;
}
  
  @-webkit-keyframes paper-plane-scoping {
    0% {
      -webkit-transform: translate(0px);
              transform: translate(0px);
    }
    100% {
      -webkit-transform: translate(400px, -100px);
              transform: translate(400px, -100px);
    }
   }
  
  @keyframes paper-plane-scoping {
    0% {
      -webkit-transform: translate(0px);
              transform: translate(0px);
    }
    100% {
      -webkit-transform: translate(400px, -100px);
              transform: translate(400px, -100px);
    }
   }
   @-webkit-keyframes rotate {
    0% {
      -webkit-transform: rotate(10deg);
              transform: rotate(10deg);
    }
    40% {
      -webkit-transform: rotate(25deg);
              transform: rotate(25deg);
    }
    50% {
      -webkit-transform: rotate(15deg);
              transform: rotate(15deg);
    }
    60% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    70% {
      -webkit-transform: rotate(1deg);
              transform: rotate(1deg);
    }
    100% {
      -webkit-transform: rotate(10deg);
              transform: rotate(10deg);
    }
  }
   @keyframes rotate {
    0% {
      -webkit-transform: rotate(10deg);
              transform: rotate(10deg);
    }
    40% {
      -webkit-transform: rotate(25deg);
              transform: rotate(25deg);
    }
    50% {
      -webkit-transform: rotate(15deg);
              transform: rotate(15deg);
    }
    60% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    70% {
      -webkit-transform: rotate(1deg);
              transform: rotate(1deg);
    }
    100% {
      -webkit-transform: rotate(10deg);
              transform: rotate(10deg);
    }
  }

